.exp-node-wrapper {
  width: 100%;
  height: 100%;
  box-sizing: border-box; }
  .exp-node-wrapper * {
    box-sizing: inherit; }

.exp-node-container {
  width: 420px;
  display: flex;
  flex-direction: column; }

.exp-node-row {
  display: flex;
  flex-direction: row; }

.exp-node-element {
  display: flex;
  flex: 1;
  margin: 5px;
  flex-direction: column; }

.exp-node-shape {
  height: 190px;
  width: 190px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url('data:image/svg+xml;utf8, <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1 1"><rect width="200" height="200" style="fill:grey;fill-opacity:0.5;" /></svg>');
  margin: 5px;
  cursor: pointer; }

.exp-node-shape-selection {
  height: 200px;
  width: 200px;
  position: absolute;
  pointer-events: none; }

.exp-node-shape-selection-active {
  border-radius: 5px;
  box-shadow: 0 0 5px 0px #cccccc; }

.exp-node-actions {
  width: fit-content;
  margin-bottom: 10px;
  border-radius: 5px;
  box-shadow: 0 0 5px 0px #cccccc;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.exp-node-description {
  width: 100%;
  border-radius: 5px;
  padding: 5px;
  box-shadow: 0 0 5px 0px #cccccc; }

.exp-node-children-actions-wrapper {
  height: 50px; }

.exp-node-children-actions {
  width: fit-content;
  border-radius: 5px;
  box-shadow: 0 0 5px 0px #cccccc;
  margin: auto; }

.exp-node-children-wrapper {
  width: 200px; }

.exp-node-children {
  width: auto;
  padding-top: 50px; }

.exp-node-delete-btn {
  height: 40px;
  width: 40px;
  padding: 6px;
  margin: 5px;
  cursor: pointer;
  user-select: none;
  border: 0;
  border-radius: 50%;
  outline: 0;
  overflow: hidden;
  background-color: #bfbfbf;
  background-image: svg-load("icons/baseline-delete-24px.svg");
  background-size: auto 70%;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 0px 0px 5px 1px rgba(143, 143, 143, 0.3);
  overflow: hidden;
  position: relative; }
  .exp-node-delete-btn:after {
    content: '';
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    width: 40px;
    height: 40px;
    margin-left: -20px;
    margin-top: -20px;
    background: rgba(255, 255, 255, 0.3);
    border-radius: 100%;
    transform: scale(0); }

@keyframes exp-node-btn-ripple {
  0% {
    transform: scale(0); }
  20% {
    transform: scale(1); }
  100% {
    opacity: 0;
    transform: scale(1); } }
  .exp-node-delete-btn:not(:active):after {
    animation: exp-node-btn-ripple 0.6s ease-out; }
  .exp-node-delete-btn:after {
    visibility: hidden; }
  .exp-node-delete-btn:focus:after {
    visibility: visible; }

